body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.header {
    height: 60px;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: #fff;
    position: -webkit-sticky;
    position: sticky;
    margin-top: 12px;
    z-index: 100;
    margin-bottom: 10px;
    top: 0;
    border-bottom: 1px solid #000;
}

.header__brand {
    font-size: 28px;
    font-weight: bold;
    font-family: 'Chilanka', cursive;
    left: 100px;
    position: absolute;
}

.header__countryDropdown {
    width: 100px !important ;
    border: none;
    font-size: 18px;
    margin-right: 100px;
    /* position: fixed; */
}

.header__search--autocomplete {
    width: 35vw ;
}
.header__search--searchicon {
    background-color: #4f4f4f;
    color: #fff;
}

.header__nav {
    display: flex;
    justify-content: space-evenly;
    text-align: left;
    font-size: 18px;
    right: 0;
    position: absolute;
    padding-right: 30px;
}
.header__nav--option {
    display: flex;
    flex-direction: column;
    margin-left: 30px;
    margin-right: 30px;
    bottom: 0;
    cursor: pointer;
}

.header__nav--option--lineTwo {
    font-size: 24px;
    font-weight: bold;
}

.header__nav--option--shopping-cart-icon {
    margin-top: 10px;
}


.home {
    display: flex;
    justify-content: center;
    /* flex-direction: row; */
    /* max-width: 1000px; */
    margin-left: auto;
    margin-right: auto;
}

.home__container {
    width: 100% !important;
}

.home__bg {
    position: relative;
    text-align: center;
}

.home__bg--bgImage {
    width: 100%;
    object-fit: contain;
}

.home__bg--brand {
    font-family: 'Chilanka', cursive;
    font-size: 108px;
    font-weight: bolder;
    color: white;
    -webkit-text-stroke-width: 0.5px;
    -webkit-text-stroke-color: black;

    position: absolute;
    top: 25%;
    left: 50%;
    transform: translate(-50%, -50%);

}
.home__bg--tagline {
    font-family: 'Roboto';
    font-size: 28px;
    font-weight: 400;
    color: black;
    /* -webkit-text-stroke-width: 0.5px;
    -webkit-text-stroke-color: black; */

    position: absolute;
    top: 38%;
    left: 50%;
    transform: translate(-50%, -50%);

}

.home__row {
    display: flex;
    flex-direction: row;
    padding: 0 30px;
}
.product {
    display: flex;
    flex-direction: column;
    font-family: 'Roboto';

    align-items: center;
    justify-content: flex-end;
    margin: 10px;
    padding: 20px;
    width: 100%;
    max-height: 400px;
    min-width: 100px;
    background-color: white;
    z-index: 1;
    border: 1px solid rgb(102, 102, 102);
    padding-bottom: 40px;
}

.product__info {
    text-align: left;
    left: 0;
}

.product__info--name {
    font-size: 24px;
    font-family: Montserrat;
}

.product__info--price {
    margin-top: -30px;
    font-family: 'Work Sans';
    font-weight: 300;
    font-size: 18px;
}

.product__info--price--permonth {
    font-weight: 500;
    font-size: 22px;
}

.product__info--useStatus {
    margin-top: -20px;
    font-weight: 400;
}

.product__img {
    max-height: 200px;
    width: 100%;
    object-fit: contain;
    margin-bottom: 15px;
}

.product_selectButton {
    width: 300px;
    height: 50px;
    font-size: 18px;
    cursor: pointer;
}

